/* Reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Table header text color */
th {
  color: black;
}

/* Body styles */
body {
  background-color: black;
  color: #fff; /* Set text color to white */
}

/* Container styles */
.container {
  max-width: 800px;
  margin: 0 auto; /* Center container */
}

/* Header styles */
.header {
  text-align: center;
  margin-bottom: 20px;
}

/* Table styles */
.custom-table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

/* Table cell styles */
.custom-table th,
.custom-table td {
  padding: 8px;
  text-align: left;
  border: 1px solid transparent; /* Initially transparent border */
}

/* Table header styles */
.custom-table th {
  background-color: #f2f2f2; /* Gray background color */
}

/* Delete button styles */
.delete-btn {
  background-color: #dc3545; /* Red background color */
  color: white; /* Text color */
  border: none; /* Remove border */
  padding: 6px 10px;
  cursor: pointer;
  border-radius: 4px;
}

/* Hover effect for delete button */
.delete-btn:hover {
  background-color: #c82333; /* Darker red background color */
}

/* Border blink animation */
@keyframes borderBlink {
  0% {
    border-color: rgb(255, 0, 0); /* Red */
  }
  50% {
    border-color: rgb(0, 255, 0); /* Green */
  }
  100% {
    border-color: rgb(0, 0, 255); /* Blue */
  }
}

/* Apply animation to all table borders */
.custom-table th,
.custom-table td {
  animation: borderBlink 3s infinite alternate; /* Apply animation */
}

/* Responsive layout */
@media screen and (max-width: 600px) {
  .custom-table {
    font-size: 12px; /* Reduce font size for smaller screens */
  }

  .custom-table th,
  .custom-table td {
    padding: 6px; /* Reduce padding for smaller screens */
  }
}
